@import url('https://fonts.googleapis.com/css2?family=IBM+Plex+Serif&family=Kosugi+Maru&family=Lora&family=M+PLUS+1p:wght@300;400&family=Noto+Serif+JP&family=Open+Sans&family=Raleway:wght@300;400&display=swap');

.main-page {
  margin: auto;
  text-align: left;
  color: gray;
  max-width: 100%;
  max-height: 100vh;
  display: flex;
}

.content {
  font-size: 1rem;
  margin-top: 1rem;
  font-family: 'M PLUS 1p', sans-serif;
  font-weight: 400;
}

.title {
  font-family: 'Noto Serif JP', serif;
  font-weight: 400;
  font-size: 2.3rem;
}

.left-content {
  height: 100vh;
  margin-left: 9%;
}

.right-content {
  margin-left: 15px;
  margin-top: 15vh;
  width: 55%;
}

.changing-image {
  max-width: 100%;
  max-height: 100%;
  opacity: .7;
  display: block;
  object-fit: cover;
}

.changing-image:hover {
  opacity: 1;
}
.icon {
  color: gray;
  margin: 0px 10px;
}

.icon:hover {
  color: black;
}

.language-label {
  position: relative;
  top: -6px;
}